<template>
  <div class="auction-box">
    <V-Scroll :scroll-height="`${mainHeight}px`">
      <ul class="tab">
        <li :class="{selected: sel == i}" v-for="(v,i) in tabList" @click="sel = i" :key="i">{{v.name}}</li>
      </ul>
      <div class="pro-list Sha">
        <div class="re-pro-single">
