          <span class="love-num">
              <em class="icon-love"></em>
              <em class="num">98</em>
            </span>
          <i class="tip sell">竞拍</i>
          <i class="tip audit">审核中</i>
        </div >
        <div class="re-pro-single">
          <img src="../../images/userInfos/headbg.png" alt="">
          <div class="info">
            <p class="name">【转卖】时尚黄色汽车儿童收纳凳</p>
            <div class="re-goods">
              <p class="price-num"><i></i><span>154</span></p>
              <!--<p class="user-info"><span>上海</span><img src="../../images/userInfos/sugar-icon.png" alt=""></p>-->
            </div>
          </div>
          <!--爱心数字-->
          <span class="love-num">
              <em class="icon-love"></em>
              <em class="num">98</em>
            </span>
          <i class="tip sell">竞拍</i>
          <i class="tip audit">审核中</i>
        </div >
        <div class="re-pro-single">
          <img src="../../images/userInfos/headbg.png" alt="">
          <div class="info">
            <p class="name">【转卖】时尚黄色汽车儿童收纳凳</p>
            <div class="re-goods">
              <p class="price-num"><i></i><span>154</span></p>
              <!--<p class="user-info"><span>上海</span><img src="../../images/userInfos/sugar-icon.png" alt=""></p>-->
            </div>
          </div>
          <!--爱心数字-->
          <span class="love-num">
              <em class="icon-love"></em>
              <em class="num">98</em>
            </span>
          <i class="tip sell">竞拍</i>
          <i class="tip audit">审核中</i>
        </div >
      </div>
    </V-Scroll>
  </div>
</template>

<script>
import Ajax from "@/mixins/ajax";

/* eslint-disable */
import "swiper/dist/css/swiper.css";

  export default {
//  name: "Home",
    data() {
      return {
        sel: 0,
        tabList: [
          {name: '竞拍中',num: 0},
          {name: '已送出',num: 1}
        ]
      };
    },
    mixins: [Ajax],
    components: {

    },
    mounted() {
      this.mainHeight = this.mainHeight - this.navigationHeight;
    },
    methods: {

    }
  };
</script>

<style scoped>
.tab{
  display: flex;
  justify-content: space-between;
  height: 1.92rem;
  line-height: 1.92rem;
  text-align: center;
  font-size: 0.54rem;
  color: #666;
  background: #fff;
}
.tab li{
  width: 50%;
}
.tab li.selected{
  color: #de2245;
  box-sizing: border-box;
  border-bottom: 0.09rem solid #de2245;
}

.pro-list{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 0.3rem;
  background: #f8f8f8;
}
.re-pro-single{
  position: relative;
  margin:0.3rem 0 0 0;
  width: 5.18rem;
  height: 7.81rem;
  border-radius: 0.36rem;
  overflow: hidden;
  background: #fff;
}
.re-pro-single>img{
  width: 5.18rem;
  height: 5.18rem;
}
.re-pro-single>.info{
  padding: 0 0.3rem;
}
.re-pro-single>.info .name{
  height: 0.97rem;
  font-size: 0.39rem;
}
.re-pro-single>.info .re-goods{
  margin-top: 0.36rem;
}
.re-pro-single>.info .re-goods,.re-goods>p{
  display: flex;
  justify-content: space-between;
}
.re-goods .price-num i{
  display: block;
  margin-right: 0.1rem;
  width: 0.67rem;
  height: 0.37rem;
  background: url("../../images/userInfos/sugar-icon.png") no-repeat center/contain;
}
.re-goods .user-info img{
  margin-left: 0.1rem;
  width: 0.49rem;
  height: 0.49rem;
}
.love-num{
  position: absolute;
  right: 0;
  top: 4.56rem;
  min-width: 1.52rem;
  height: 0.52rem;
  line-height: 1.5;
  background-color: rgba(0, 0, 0, 0.4);
  border-top-left-radius: 0.26rem;
  border-bottom-left-radius: 0.26rem;
  font-size: 0.36rem;
  color: #fff;
}
.love-num .icon-love{
  display: inline-block;
  width: 0.44rem;
  height: 0.37rem;
  background: url("../../images/home/dis_collect.png") no-repeat;
  background-size: 100%;
  margin-left: 0.2rem;
  vertical-align: -1px;
}
.love-num .num{
  color: #fff;
  font-size: 0.36rem;
  font-weight: normal;
}

.re-pro-single .tip{
  font-style: normal;
  display: block;
  position: absolute;
  top: 0;
  padding: 0 0.2rem;
  height: 0.58rem;
  line-height: 0.58rem;
  text-align: center;
  font-size: 0.24rem;
  color: #fff;
}
.re-pro-single .tip.sell{
  background: #d11135;
  border-bottom-right-radius: 0.36rem;
}
.re-pro-single .tip.audit{
  right: 0;
  background: #aaa;
  border-bottom-left-radius: 0.36rem;
}
</style>



